export default {
  getMonthText: function (month) {
    const monthArray = ['','','','','','','','','','','','',];
    return monthArray[month];
  },
  getWeekText: function (day) {
    let weekText = '  ';
    switch (day) {
      case 0:
        weekText = '  ';
        break;
      case 1:
        weekText = '  ';
        break;
      case 2:
        weekText = '  ';
        break;
      case 3:
        weekText = '  ';
        break;
      case 4:
        weekText = '  ';
        break;
      case 5:
        weekText = '  ';
        break;
      case 6:
        weekText = '  ';
        break;
      default:
        weekText = '  ';
    }
    return weekText;
  },
  getFirstDay: function (day) {
    let date = new Date(day);
    let first = new Date(date.getFullYear(), date.getMonth(), 1);
    return first.getTime();
  },
  getLastDay: function (day) {
    let date = new Date(day);
    let last = new Date(date.getFullYear(), date.getMonth() + 1, 0);
    return last.getTime();
  }
}



